import moment from 'moment'
import navBar from '@/components/navbar/navbar.vue'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
import api from '@/api/api.js'

export default {
	data() {
		return {
			content:{},
			navText:'主题',
			isShare:false,
			ID:'',
			badgeColor:'',
			tab:'',
			replyValue:'',
			//loginname:uni.getStorageSync('token')?token.loginname:''
		}
	},
	components: {
		navBar,
		uniBadge
	},
	onLoad(option) {
		this.ID = option.id;
		this.badgeColor = option.badgeColor;
		this.tab = option.tab;
		this.getReplies(option.id);
	},
	onReady() {

	},
	filters: {
		formatDate(time) {
			let _time, data;
			if (time) {
				_time = time.split('T');
				data = moment(_time[0]).locale('zh-cn').fromNow();
			}

			return data
		}
	},
	methods: {
		async getReplies(id) {
			const [error, data] = await this.$awaitWrap(api.replies({
				id: id

			}))
			if (data[1]) {
				let content = this.content = data[1].data.data;
				content.content = this.$richStyle(this.content.content);
				//content.replies = this.$richStyle(this.content.replies);
				content.replies.forEach(item =>{
					item.content = this.$richStyle(item.content)
				})
					
			}else{
				console.log(error)
			}
		},
		async setUps(id,index){
			let token = uni.getStorageSync('token');
			if(!token){
				uni.navigateTo({
					url:'/pages/login/login',
				})
				return
			}
			const data = await api.Ups({
				method: 'post',
				replyid: id,
				query:{
					accesstoken:token
				}
			})
			this.content.replies.forEach((item,key,arr)=>{
				if(index == key){
					if(data[1].data.action =="up"){
						item.isUp = true
						item.ups.splice(-1,0,token)
						item.upColor = true
					}else if((data[1].data.action =="down")){
						item.isUp = false
						item.ups.splice(-1,1)
						item.upColor = false
					}
					this.$forceUpdate()
				}
			})
			
		},
		async subReply(){
			let token = uni.getStorageSync('token');
			if(!token){
				uni.navigateTo({
					url:'/pages/login/login',
				})
				return
			}
			const [error,data] = await api.Nreply({
				method:'post',
				topicid:this.ID,
				query:{
					accesstoken:token,
					content:this.replyValue,
				}
			})
			if(data.data.success){
				this.replyValue = '';
				this.$Tips.alert('提交成功');
				this.getReplies(this.ID)
			}else{
				this.$Tips.alert(data.data.error_msg);
			}
		},
		async deleReply(){
			let token = uni.getStorageSync('token');
			if(!token){
				uni.navigateTo({
					url:'/pages/login/login',
				})
				return
			}
			const [error,data] = await api.deleReply({
				method:'post',
				query:{
					reply_id:token
				}
			})
		}
		
	},
	onShareAppMessage(res) {
	    if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
	    return {
	      title: res.target.dataset.info,
	      path: '/pages/details/index?id='+this.ID
	    }
	  }
}
